import React from 'react'
import { Typography, Paper } from '@material-ui/core'
import makeStyles from '@material-ui/core/styles/makeStyles'
import PropTypes from 'prop-types'

EmptyData.propTypes = {
  title: PropTypes.string,
  children: PropTypes.any,
  variant: PropTypes.oneOf(['outlined', 'elevation'])
}

const useStyles = makeStyles((theme) => ({
  center: {
    textAlign: 'center',
    padding: theme.spacing(2)
  },
  marginTop: {
    marginTop: theme.spacing(3)
  },
  marginBottom: {
    marginBottom: theme.spacing(3)
  },
  emptyPath: {
    fill: '#fafafa'
  },
  emptyEllipse: {
    fill: '#f5f5f5'
  },
  g: {
    stroke: '#d9d9d9'
  },
  simple: {
    height: '100%',
    margin: 'auto'
  }
}))

const Svg = () => {
  const classes = useStyles()
  return (
    <svg
      className={classes.simple}
      width='64'
      height='41'
      viewBox='0 0 64 41'
      xmlns='http://www.w3.org/2000/svg'
    >
      <g transform='translate(0 1)' fill='none' fillRule='evenodd'>
        <ellipse
          className={classes.emptyEllipse}
          cx='32'
          cy='33'
          rx='32'
          ry='7'
        />
        <g className={classes.g} fillRule='nonzero'>
          <path d='M55 12.76L44.854 1.258C44.367.474 43.656 0 42.907 0H21.093c-.749 0-1.46.474-1.947 1.257L9 12.761V22h46v-9.24z' />
          <path
            d='M41.613 15.931c0-1.605.994-2.93 2.227-2.931H55v18.137C55 33.26 53.68 35 52.05 35h-40.1C10.32 35 9 33.259 9 31.137V13h11.16c1.233 0 2.227 1.323 2.227 2.928v.022c0 1.605 1.005 2.901 2.237 2.901h14.752c1.232 0 2.237-1.308 2.237-2.913v-.007z'
            className={classes.emptyPath}
          />
        </g>
      </g>
    </svg>
  )
}

export function EmptyData(props) {
  const classes = useStyles()
  const { title, children, variant } = props
  return (
    <Paper variant={variant || 'outlined'}>
      <div className={classes.center}>
        <Svg />
        <Typography variant='subtitle1' color='primary'>
          {title}
        </Typography>
        {children}
      </div>
    </Paper>
  )
}
